<template>
  <div class="rule-wrapper">
    <div class="rule-content">
      <div class="ticket-content">
        <div v-for="(item, index) in prizeList" :key="index">
          <div class="ticket-content-item">
            <div class="first-cloumn">
              <p class="name">成功兑换{{item.awardNumber}}个{{item.awardEntity.awardName}}</p>
              <p class="common-text">{{item.recordTime}}</p>
            </div>
            <div v-if="getCarsType(item.awardEntity.index)" class="second-cloumn">
              <p class="common-text">{{getCardsName(item.awardNumber,item.awardEntity.index)}}</p>
            </div>
            <div v-else class="second-other-cloumn">
                  <p class="common-text">-{{item.awardNumber}}鼠卡</p>
                  <p class="common-text">-{{item.awardNumber}}牛卡</p>
            </div>
            <div class="third-cloumn">
              <p class="common-text">获得{{item.awardNumber}}个</p>
              <p class="common-text">{{item.awardEntity.awardNameAlias}}</p>
            </div>
          </div>
        </div>
        <!-- <p class="gift-no-tip" v-if="prizesFlag">{{noRecordTip}}</p> -->
      </div>
    </div>
    <div>
      <span class="advice">投资有风险 入市需谨慎</span>
    </div>
  </div>
</template>

<script>

import { myAwards } from '@/api/award'
import { Toast, Indicator } from 'mint-ui';
export default {
  name: 'AwardRecord',
  components: {

  },
  data() {
    return {
      prizeList: [],
      prizesFlag: false,
    }
  },
  computed: {
   
  },
  created() {
    let titleObject = {
      back: true,
      romoveMarginTop: false,
      title: "奖品兑换记录"
    }
    this.$emit("routerChange", titleObject);

    this.getMyAwards();
  },
   methods: {
      getMyAwards() {
      Indicator.open({
        text: '加载中...',
        spinnerType: 'triple-bounce'
      });
      myAwards().then((res) => {
        console.log(res);
        Indicator.close();
        if (res.data.resCode == 0) {
          //成功请求数据
          if (res.data.result.length > 0) {
            this.prizeList = res.data.result;
            this.prizesFlag = true;
          }else {
             this.prizesFlag = false;
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    },
     getCardsName(number,index) {
      let carsDetail = "";
        if (index == 1) {//“牛气冲天”奖：1张牛生肖卡
          carsDetail = "-" + number + "张牛卡";
        }else if (index == 2) {//“鼠一鼠二”奖：2张鼠生肖卡
          carsDetail = "-" + number*2 + "张鼠卡"
        }else if (index ==4) {//“全家福”大奖：12种不同生肖卡各1张
          carsDetail = "-" + number*12 + "张生肖卡"
        }
        return carsDetail;
    },
    getCarsType(index) {
      let isSingle = true;
        if(index == 3) {//“鼠你最牛”奖：1张鼠生肖卡+1张牛生肖卡
          this.isSingle = false;
        }else {
          this.isSingle = true;
        }
        return isSingle;
    }
   }
}
</script>
